/*
================================================================================
|                                     HEADER                                   |
================================================================================
*/
header { width: 100%; min-width: 320px; font-family: "Roboto", sans-serif; font-weight: 300; font-size: 14px; }

.header { padding: 15px 0 25px 0; }

.header .wrapper { display: flex; align-items: center; justify-content: space-between; position: relative; }

.header-logo { height: auto; display: flex; align-items: center; margin: 0 50px 0 0; }

.header-logo__img { display: block; height: auto; }

.header-logo__text { font-family: 'Museo-Sans'; font-size: 22px; margin-left: 10px; text-transform: uppercase; white-space: nowrap; }

.menu { display: flex; box-sizing: border-box; width: 100%; padding: 10px 0; align-items: center; justify-content: space-between; flex-flow: row wrap; }

.menu__item { display: inline-block; }

.menu__link { display: block; padding: 5px 0; margin: 5px 25px 5px 0; border-bottom: 1px solid transparent; transition: all .5s; }

.menu__link:hover { color: #b3b3b3; border-bottom: 1px solid #333; box-sizing: content-box; }

.menu__nav { margin: 0; padding: 0; font-size: 18px; }

.menu__language .menu__link { margin: 0; padding: 4px; transition: all .5s; border: 1px solid transparent; }

.menu__language .menu__link:hover, .menu__language .menu__link:active, .menu__language .menu__link:focus, .menu__language .menu__link.active { border-bottom: none; border: 1px solid #b7b6b4; }

.menu__for-visitors .menu__link { margin: 0 7px 0 0; }

.btn__burger { display: none; }

@media screen and (max-width: 992px) { .menu { position: absolute; display: none; left: 0; top: 69px; background-color: #ddd; z-index: 10; flex-direction: column; }
  .menu__nav, .menu__language { border-bottom: 1px solid #d6d0c9; }
  .menu__language { text-align: center; }
  .menu__language li.menu__item { display: inline-block; }
  .btn__burger { display: block; }
  .menu__item { display: block; margin: 15px; text-align: center; } }

