.footer { width: 100%; min-width: 320px; padding: 20px 0; }

.footer .wrapper { display: flex; flex-direction: column; align-items: center; }

.footer__contact-number { cursor: pointer; display: inline-block; margin-right: 25px; margin-bottom: 35px; }

.footer__contact-number .icon-tel { border-radius: 50%; border: 1px solid #383838; padding: 7px; font-size: 16px; }

.footer__contact-number:hover .footer__number { color: #999999; }

.footer__number { display: inline-block; font-size: 18px; margin-left: 10px; }

.footer__number span { font-weight: 500; }

.footer__social-icons { margin-bottom: 30px; display: flex; justify-content: center; }

.footer__social-icons a { cursor: pointer; border: 1px solid #333333; padding: 7px; font-size: 14px; margin: 0 5px; transition: all .5s; }

.footer__social-icons a:hover { box-shadow: 0 0 5px 1px #222222; }

.footer__social-icons .icon-fb { padding: 7px 11px; }

.footer__social-icons .icon-fb:hover { color: #fff; background-color: #5C54B6; }

.footer__social-icons .icon-vk:hover { color: #fff; background-color: #5C54B6; }

.footer__social-icons .icon-instagram:hover { color: #fff; /*background-color: #FF007B;*/ background: radial-gradient(circle at 30% 110%, #fdf497 0%, #fdf497 10%, #fd5949 40%, #d6249f 60%, #285AEB 100%); }

.footer__info { font-family: "Roboto", sans-serif; font-weight: 300; font-size: 14px; }

.footer__info a { margin: 5px; border-bottom: 1px solid transparent; }

.footer__info a:hover { color: black; border-bottom: 1px solid #333; }

@media screen and (min-width: 500px) { .footer .wrapper { flex-direction: row; justify-content: space-between; } }

@media screen and (min-width: 768px) { .footer__contact-number { margin-bottom: 0; }
  .footer__social-icons { display: inline-block; margin-bottom: 0; } }

