.order-form__price { font-size: 16px; font-weight: 400; flex-basis: auto; }

.order-form__price span { margin-left: 5px; font-weight: 500; font-size: 22px; }

.order-form__btn { outline: none; border: none; height: 55px; padding: 0 45px; background-color: #642121; color: #fff; text-transform: uppercase; font-weight: 700; font-size: 18px; transition: background-color .5s; }

.order-form__btn:hover { background-color: #9e3434; }

.ui-datepicker-trigger { position: absolute; right: 3%; top: 15%; cursor: pointer; }

.checkbox-1 { cursor: pointer; font-weight: 400; }

.checkbox-1__check { display: block; position: relative; padding: 5px 10px 9px 30px; transition: all .5s; border: 1px solid transparent; }

.checkbox-1__check:hover { border: 1px solid #aa4141; }

.checkbox-1__check:before { display: inline-block; content: ''; position: absolute; left: 10px; top: 33%; width: 10px; height: 10px; border-radius: 50%; border: 2px solid #9d9d9d; }

.checkbox-1 input { display: none; }

.checkbox-1 input:checked ~ .checkbox-1__check { display: inline-block; background-color: #aa4141; color: #fff; }

.checkbox-1 input:checked + .checkbox-1__check:before { width: 15px; height: 10px; border: none; top: 33%; background: url(../img/icons/checked.png) no-repeat center; }

/*=======loader======*/
.loaded .main-wrapper { visibility: hidden; }

.icon-load { background: url(../img/loader.gif) no-repeat left top; width: 40px; height: 40px; position: fixed; left: 50%; top: 50%; margin-left: -20px; margin-left: -20px; display: none; }

.loaded .icon-load { display: block; }

/*=======loader======*/
@media screen and (min-width: 500px) { .about-us__assortment { flex-basis: 48%; }
  .order-form__item_label-size-sm .order-form__label { flex-basis: 50px; }
  .order-form__item_label-flex-gr { flex-grow: 1; }
  .order-form__item_label-size-sm .order-form__label { flex-basis: 60px; margin-left: 30px; }
  .order-form__item_date, .order-form__item_family-members { flex-basis: 45%; }
  .order-form__item_type-number { flex-basis: 55%; }
  .order-form__item_family-members:last-child .order-form__label { margin-left: 40px; flex-basis: 50px; } }

@media screen and (min-width: 768px) { .about-us__assortment { flex-basis: 31%; }
  .order-form__additional-services { flex-direction: row; flex-wrap: wrap; }
  .order-form__price, .order-form__subtitle { flex-basis: 100%; } }

@media screen and (min-width: 992px) { .about-us__catalog { width: 90%; }
  .content .wrapper { display: flex; align-items: flex-start; }
  .about-us { flex-basis: 49%; }
  .order-form { flex-basis: 38%; margin: 20px 0 0 65px; }
  .order-form__item { margin: 0 0 18px 0; }
  .order-form__subtitle { margin: 30px 0 5px 0; }
  .order-form__price { margin: 30px 0 0 0; }
  .order-form__item_type-number { flex-basis: 100%; } }

@media screen and (min-width: 1200px) { .order-form__item_type-number, .order-form__item_wrap-family-members { flex-basis: 70%; } }

